.container {
  align-items: flex-end;
  bottom: 5vh;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  gap: 5%;
  height: 70vh;
  background-color: rgba(0, 0, 0, 0.4);
  transition: visibility 0.7s, left 0.7s ease;
}

.store {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.backdrop {
  background-image: url("../../images2/tlo5.jpg");
  background-size: 50vh;
  background-repeat: repeat;
  position: absolute;
  width: 100%;
  height: 100%;
  filter: hue-rotate(72deg);
}

.storeAppear {
  opacity: 0;
}

.storeAppearActive {
  opacity: 0;
}

.storeEnter {
  bottom: -100vh;
}

.storeEnterActive {
  animation-name: bounce-in;
  animation-duration: 0.6s;
  animation-timing-function: ease;
  bottom: 0;
}

.storeEnterDone {
  bottom: 0;
}

.storeExit {
  bottom: 0;
}

.storeExitActive {
  animation-name: bounce-in;
  animation-duration: 0.6s;
  animation-timing-function: ease;
  animation-direction: reverse;
  bottom: -100vh;
}

.storeExitDone {
  opacity: -100vh;
}

@keyframes bounce-in {
  0% {
    bottom: -100vh;
  }
  70% {
    bottom: 10vh;
  }
  100% {
    bottom: 0;
  }
}

.button {
  right: 2%;
  top: 2%;
  position: absolute;
  font-family: viper-font;
  font-size: 60px;
  color: goldenrod;
  width: 60px;
  height: 60px;
  border: none;
  background: none;
  cursor: pointer;
}

.button:hover {
  animation: hueRotate 0.5s linear infinite;
}

.previous {
  position: absolute;
  transform: rotateY(180deg);
  left: 10%;
  top: 25%;
  cursor: pointer;
  border: none;
  background: none;
  z-index: 11;
  width: 7vw;
  opacity: 80%;
}
.next {
  position: absolute;
  left: 90%;
  transform: translate(-100%, 0);
  top: 25%;
  cursor: pointer;
  border: none;
  background: none;
  z-index: 11;
  width: 7vw;
  opacity: 80%;
}

.previous:hover {
  opacity: 100%;
  width: 7.5vw;
  transition: width 0.1s;
}

.next:hover {
  opacity: 100%;
  width: 7.5vw;
  transition: width 0.1s;
}

.title {
  height: 100%;
}

.titleButton {
  height: 20%;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  cursor: wait;
  background: none;
  border: none;
}

.title:hover {
  animation: hueRotate 0.5s linear infinite;
}
@keyframes hueRotate {
  from {
    filter: hue-rotate(0);
  }
  to {
    filter: hue-rotate(360deg);
  }
}

.footer {
  position: absolute;
  color: rgba(240, 248, 255, 0.3);
  font-size: 15px;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 10px;
}
